.admin{
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}
.admin-left-image{
    border-radius: 50%;
}
.admin-right{
    background: rgba(23, 35, 51, 0.7);
    box-shadow: inset 0px 1px 2px rgba(239, 232, 232, 0.4);
    backdrop-filter: blur(30px);
    border-radius: 20px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 40px;
    padding-bottom: 40px;
}
.admin-right-heading{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 130%;
    color: #FFFFFF;
}
.admin-right-padding{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    color: #FFFFFF;
}
.admin-regist{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.box{
    background: rgba(23, 35, 51, 0.7);
    box-shadow: inset 0px 1px 2px rgba(239, 232, 232, 0.4);
    backdrop-filter: blur(30px);
    border-radius: 20px;
    padding-left: 70px;
    padding-right: 70px;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    margin-bottom: 50px;
}
.box-heading{
    width: 200px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 130%;
    color: #FFFFFF;
}
.admin-heading{
    display: flex;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 52px;
    line-height: 130%;
    color: #FFFFFF;
    margin-left: 400px;
}