.header-nav{
    display: flex;
    justify-content: space-between;
    padding-top: 60px;
}
.Header-right{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.8;
    list-style-type: none;
    text-decoration: none;
}
.Header-left{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.8;
    list-style-type: none;
    text-decoration: none;
    background: rgba(255, 255, 255, 0.06);
    box-shadow: inset 0px 1px 2px rgba(239, 232, 232, 0.3);
    backdrop-filter: blur(8px);
    border-radius: 8px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 20px;
    padding-left: 50px;
    margin: 0;
}
.Header-left-heading{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.8;
    list-style-type: none;
    text-decoration: none;
    position: relative;
    bottom: 12px;
}
.Header-left-image{
    margin-right: 10px;
    border-radius: 50%;
}